/**
 * Integration tests for Goal and Streak system integration
 *
 * Tests the integration between the existing goal system and the new streak tracking system
 */

import {
    completeGoal,
    createGoal,
    getGoalStreak,
} from "@/services/firebase/goalService";
import { streakService } from "@/services/firebase/streakService";

// Mock Firebase
jest.mock("../firebaseConfig", () => ({
    db: {},
    APP_ID: "test-app",
}));

// Mock Firestore functions
jest.mock("firebase/firestore", () => ({
    addDoc: jest.fn(),
    doc: jest.fn(),
    getDoc: jest.fn(),
    getDocs: jest.fn(),
    query: jest.fn(),
    where: jest.fn(),
    orderBy: jest.fn(),
    runTransaction: jest.fn(),
    serverTimestamp: jest.fn(() => ({ seconds: Date.now() / 1000 })),
    Timestamp: {
        fromDate: jest.fn((date) => ({
            toDate: () => date,
            toMillis: () => date.getTime(),
            seconds: Math.floor(date.getTime() / 1000),
            nanoseconds: (date.getTime() % 1000) * 1000000,
        })),
        now: jest.fn(() => ({
            toDate: () => new Date(),
            toMillis: () => Date.now(),
            seconds: Math.floor(Date.now() / 1000),
            nanoseconds: (Date.now() % 1000) * 1000000,
        })),
    },
    updateDoc: jest.fn(),
    onSnapshot: jest.fn(),
    collection: jest.fn(),
}));

// Mock collections
jest.mock("@/services/firebase/collections", () => ({
    getGoalsCollection: jest.fn(() => ({ path: "goals" })),
    getGoalDoc: jest.fn(() => ({ path: "goal-doc" })),
    getHabitStreaksCollection: jest.fn(() => ({ path: "streaks" })),
    getHabitCompletionsCollection: jest.fn(() => ({ path: "completions" })),
}));

// Mock streak service methods
jest.mock("@/services/firebase/streakService", () => ({
    streakService: {
        initializeHabitStreak: jest.fn(),
        recordCompletionWithMilestoneCheck: jest.fn(),
        getHabitStreak: jest.fn(),
    },
}));

// Mock error handling
jest.mock("@/utils/errorHandling", () => ({
    getUserFriendlyErrorMessage: jest.fn(
        (error) => error.message || "Unknown error",
    ),
    retryWithBackoff: jest.fn((fn) => fn()),
    validateGoalInput: jest.fn(() => null),
}));

describe("Goal-Streak Integration", () => {
    const mockUserId = "test-user-123";
    const mockGoalInput = {
        description: "Test Habit",
        frequency: "daily" as const,
        targetDays: ["Monday", "Tuesday", "Wednesday", "Thursday", "Friday"],
        difficulty: "medium" as const,
    };

    beforeEach(() => {
        jest.clearAllMocks();

        // Mock successful goal creation
        const { addDoc } = require("firebase/firestore");
        addDoc.mockResolvedValue({ id: "goal-123" });

        // Mock successful transaction
        const { runTransaction } = require("firebase/firestore");
        runTransaction.mockImplementation((db, fn) =>
            fn({
                get: jest.fn().mockResolvedValue({ exists: () => false }),
                set: jest.fn(),
            }),
        );
    });

    describe("Goal Creation Integration", () => {
        it("should initialize streak tracking when creating a new goal", async () => {
            const goalId = await createGoal(mockUserId, mockGoalInput);

            expect(goalId).toBe("goal-123");
            expect(streakService.initializeHabitStreak).toHaveBeenCalledWith(
                "goal-123",
                mockUserId,
            );
        });

        it("should not fail goal creation if streak initialization fails", async () => {
            // Mock streak initialization failure
            (streakService.initializeHabitStreak as jest.Mock).mockRejectedValue(
                new Error("Streak init failed"),
            );

            const goalId = await createGoal(mockUserId, mockGoalInput);

            expect(goalId).toBe("goal-123");
            expect(streakService.initializeHabitStreak).toHaveBeenCalled();
        });
    });

    describe("Goal Completion Integration", () => {
        const mockGoal = {
            id: "goal-123",
            ownerId: mockUserId,
            description: "Test Habit",
            frequency: "daily" as const,
            targetDays: ["Monday", "Tuesday", "Wednesday", "Thursday", "Friday"],
            difficulty: "medium" as const,
            latestCompletionDate: null,
            currentStatus: "Yellow" as const,
            nextDeadline: new Date(),
            isShared: true,
            createdAt: new Date(),
            redSince: null,
        };

        it("should record completion in streak system when completing a goal", async () => {
            // Mock successful streak recording
            (
                streakService.recordCompletionWithMilestoneCheck as jest.Mock
            ).mockResolvedValue({
                completion: { id: "completion-123" },
                newMilestones: [],
                updatedStreak: { currentStreak: 1, bestStreak: 1 },
            });

            await completeGoal("goal-123", mockGoal);

            expect(
                streakService.recordCompletionWithMilestoneCheck,
            ).toHaveBeenCalledWith(
                expect.objectContaining({
                    habitId: "goal-123",
                    userId: mockUserId,
                    difficulty: "medium",
                }),
            );
        });

        it("should log milestone achievements when completing a goal", async () => {
            const consoleSpy = jest.spyOn(console, "log").mockImplementation();

            // Mock milestone achievement
            (
                streakService.recordCompletionWithMilestoneCheck as jest.Mock
            ).mockResolvedValue({
                completion: { id: "completion-123" },
                newMilestones: [{ days: 7, achievedAt: new Date(), celebrated: false }],
                updatedStreak: { currentStreak: 7, bestStreak: 7 },
            });

            await completeGoal("goal-123", mockGoal);

            expect(consoleSpy).toHaveBeenCalledWith(
                expect.stringContaining("New milestones achieved"),
                expect.any(Array),
            );

            consoleSpy.mockRestore();
        });
    });

    describe("Streak Retrieval Integration", () => {
        it("should retrieve streak information for a goal", async () => {
            const mockStreak = {
                habitId: "goal-123",
                userId: mockUserId,
                currentStreak: 5,
                bestStreak: 10,
                lastCompletionDate: "2024-01-15",
                streakStartDate: "2024-01-11",
                freezesAvailable: 1,
                freezesUsed: 0,
                milestones: [],
            };

            (streakService.getHabitStreak as jest.Mock).mockResolvedValue(mockStreak);

            const streak = await getGoalStreak("goal-123", mockUserId);

            expect(streak).toEqual(mockStreak);
            expect(streakService.getHabitStreak).toHaveBeenCalledWith(
                "goal-123",
                mockUserId,
            );
        });

        it("should return null if streak retrieval fails", async () => {
            (streakService.getHabitStreak as jest.Mock).mockRejectedValue(
                new Error("Streak not found"),
            );

            const streak = await getGoalStreak("goal-123", mockUserId);

            expect(streak).toBeNull();
        });
    });
});
